import React, { useEffect } from 'react'
import {  Link, NavLink } from 'react-router-dom'




export default function Navbar(props) {
  useEffect(()=>{
    console.log(props.data);
  },[props.data])
  return (
    <nav className="navbar navbar-expand-lg shadow-lg navbar-dark">
  <div className="container-fluid">
    <Link className="navbar-brand " to="home" >CimaMovie~</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon" />
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      {props.data?<ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink className={`nav-link `} aria-current="page" to="home">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className={`nav-link `} to="movies" >Movies</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className={`nav-link `} to="people" >People</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className={`nav-link `}  to="tvshow">TV Show</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className={`nav-link `} to="search" >Search</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className={`nav-link `} to="about" >About</NavLink>
        </li>
      
        
      </ul>:''}

      <ul className="navbar-nav ms-auto mb-2 mb-lg-0 d-flex align-items-center">
       {props.data?<>
        <li className="nav-item">
          <span className="nav-link Logout" onClick={props.logOut} >Logout</span>
        </li>
       </>:<>
      
        <li className="nav-item">
          <Link className="nav-link" to="register" >Register</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="login" >Login</Link>
        </li>
       </>}
      

       
       
      </ul>
      
    </div>
  </div>
</nav>

  )
}
