
import { useContext } from 'react'
import style from './Home.module.css'
import { Trending } from '../Context/ApiContext';
import { Helmet } from 'react-helmet';


export default function Home() {
  let {moviesArr , tvArr , peopleArr ,imgsrc ,load}=useContext(Trending);
  return (
    <>
<Helmet>
     <title>CimaMovie~Home</title>
      </Helmet>
      {load ? <div className='d-flex min-vh-100 align-items-center justify-content-center '><i className="fa-solid fa-circle-notch fa-spin fa-10x"></i></div> : <>

        <div className="row g-3 ">
          <div className='col-md-4 d-flex align-items-center'>
            <div>

              <div className={style.brdtop}></div>
              <h2 className='pt-2'> Trending <br /> Movies <br /> To Watch Now</h2>
              <p className='text-white-50 pb-2'>Lorem ipsum dolor sit.</p>
              <div className={style.brdbottom}></div>

            </div>
          </div>
          {moviesArr.slice(10).map((item, index) =>
            <div className='col-md-2 col-6 ' key={index} data-aos="zoom-in-down">
              <div className='text-center itemsContainer'>
                <img src={imgsrc + item.poster_path} className="w-100" alt="" />
                <div className="layer">
                <p>{item.title}</p>
                </div>
                {item.vote_average===0?'':<div className="vote">{item.vote_average.toString().slice(0,3)}</div>}
                
              </div>
            </div>
          )}
        </div>


        <div className="row g-3 my-3">
          <div className='col-md-4 d-flex align-items-center'>
            <div>

              <div className={style.brdtop}></div>
              <h2 className='pt-2'> Trending <br /> TV <br /> To Watch Now</h2>
              <p className='text-white-50 pb-2'>Lorem ipsum dolor sit.</p>
              <div className={style.brdbottom}></div>

            </div>
          </div>
          {tvArr.slice(10).map((item, index) =>
            <div className='col-md-2 col-6 ' data-aos="zoom-in-down" key={index} >
              <div className='text-center itemsContainer'>
                <img src={imgsrc + item.poster_path} className="w-100" alt="" />
               <div className="layer">
               <p>{item.name}</p>
               </div>
               {item.vote_average===0?'':<div className="vote">{item.vote_average.toString().slice(0,3)}</div>}
              </div>
            </div>
          )}
        </div>


        <div className="row g-3  mb-3">
          <div className='col-md-4  d-flex align-items-center'>
            <div>

              <div className={style.brdtop}></div>
              <h2 className='pt-2'> Trending <br /> People <br /> To Watch Now</h2>
              <p className='text-white-50 pb-2'>Lorem ipsum dolor sit.</p>
              <div className={style.brdbottom}></div>

            </div>
          </div>
          {peopleArr.filter((human) => human.profile_path != null).slice(0,10).map((item, index) =>
            <div className='col-md-2 col-6 'data-aos="zoom-in-down" key={index} >
              <div className='text-center itemsContainer'>
                <img src={imgsrc + item.profile_path} className="w-100" alt="" />
                <div className="layer">
                <p>{item.name}</p>
                </div>
              </div>
            </div>
          )}
        </div>





      </>}



    </>
  )
}
