import axios from 'axios'
import Joi from 'joi'
import React,{useState} from 'react'
import { Helmet } from 'react-helmet'
import { useNavigate } from 'react-router-dom'


export default function Register() {
  let navigate =useNavigate()
  let [errvalidat,setErrValidat]=useState([]) //joi err
  let[errMessage,setErrMessage]=useState('') //api err
let [user,setUser]=useState({
  "first_name":"",
  "last_name":"",
  "age":"",
  "email":"",
  "password":""
})
const [loading, setLoading] = useState(false)
function getInputData(e){
  let newUser={...user}
  newUser[e.target.id]=e.target.value
  setUser(newUser)
  console.log(user);
}



function submitForm(e){
 
  
e.preventDefault()

let errvalidation =validation()
if(errvalidation.error){
  setErrValidat(errvalidation.error.details)
  
  
}else{
  setErrValidat([])
  getApi(user)
 
}
}





async function getApi(someinfo){
  setLoading(true)
  let {data} =await axios.post(`https://movies-api.routemisr.com/signup`,someinfo)
  setLoading(false)
  console.log(data);
  if(data.message === 'success'){
    setErrMessage('')

    navigate('/')

  }else{
    setErrMessage(data.message)

  }
}

function validation(){
  const schema = Joi.object({
    first_name: Joi.string()
        .alphanum()
        .min(3)
        .max(30)
        .required(),
        last_name: Joi.string()
        .alphanum()
        .min(3)
        .max(30)
        .required(),

    password: Joi.string()
        .pattern(new RegExp('^[a-zA-Z0-9]{3,30}$')),

    email: Joi.string()
        .email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }),
        age: Joi.number()
        .min(16)
        .max(60)
        .required(),
  });
 return schema.validate(user,{abortEarly:false});
}

  return (
   <>
    <Helmet>
     <title>CimaMovie~Register</title>
      </Helmet>
    <div className='py-5'>
      <h2>Registration Form</h2>
      {errMessage?<p className='alert alert-info'>{errMessage}</p>:''}
      <div className='row'>
        {errvalidat.map((item,index)=><>
        <div className='col' key={index}>
          <div>
            <p className='alert alert-danger'>{item.message}</p>
          </div>
        </div>
        </>)}
      </div>
      <form onSubmit={submitForm}>
        <label htmlFor="first_name">First Name</label>
        <input type="text" className='form-control my-2' id='first_name'  onChange={getInputData}/>
        <label htmlFor="last_name">Last Name</label>
        <input type="text" className='form-control my-2' id='last_name' onChange={getInputData} />
        <label htmlFor="age">Age</label>
        <input type="text" className='form-control my-2' id='age' onChange={getInputData} />
        <label htmlFor="email">Email</label>
        <input type="text" className='form-control my-2' id='email' onChange={getInputData} />
        <label htmlFor="password">Password</label>
        <input type="password" className='form-control my-2' id='password' onChange={getInputData} />
        {loading?<button type='submit' className='btn btn-info float-end'><i className='fa fa-spin fa-spinner'></i></button>:<button type='submit' className='btn btn-info float-end'>register</button>}
        
        
      </form>
    </div>
   
   </>
  )
}
