import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { Trending } from '../Context/ApiContext'
import { Link } from 'react-router-dom'
import { Helmet } from 'react-helmet'

export default function Search() {
  let {imgsrc  }=useContext(Trending)
  const [load, setLoad] = useState(false)
  let [searchMovie, setSearchMovie] = useState([])
  const [nameMovie, setNameMovie] = useState('')
  const [mediaMovie, setMediaMovie] = useState('movie')
  async function getTeendingData(mediatype , MovieName) {
    setLoad(true)
    let { data } = await axios.get(`https://api.themoviedb.org/3/search/${mediatype}?query=${MovieName}&api_key=54a067480c37b2809731dbbe98a6e0ab`)
    setSearchMovie(data.results);
    setLoad(false)
    
  }

console.log(searchMovie);
  function sendData(){
    getTeendingData(mediaMovie ,nameMovie)
  }

function getMedia(e){
  setMediaMovie(e.target.innerText)
}
function getName(e){
  setNameMovie(e.target.value)
}
  return (
   <>
   <Helmet>
     <title>CimaMovie~Search~{nameMovie}</title>
      </Helmet>
  {load?<div className='d-flex min-vh-100 align-items-center justify-content-center '><i className="fa-solid fa-circle-notch fa-spin fa-10x"></i></div>:<>
    <div className='CimaSearch mt-5'>
        <h1>Search Now ~</h1>
        <div className="row justify-content-end align-items-center gx-2 gy-1">
       
         
          <div className='col-md-2 col '>
            <button className='btn btn-primary  w-100' onClick={getMedia}>movie</button>
          
          </div>
          <div className='col-md-2 col '>
          <button className='btn btn-primary  w-100' onClick={getMedia}>tv</button>          
          </div>
          <div className="col-md-6 col-12  ">
          <input type="search" onChange={getName}  className='form-control serchinputt' placeholder='SearchNow' />
          </div>
          <div className="col-md-2 col  ">
            <button className='btn btn-primary'  onClick={sendData}>SearchNow</button>
          </div>
        </div>
   </div>
   
{searchMovie?<>

  <div className='serchResults mt-5'>
  <div className="row pt-4 g-3 section">
   {searchMovie.filter((item)=>item.poster_path != undefined).map((item,index)=>
  <div className='col-md-3' key={index} >
  <div className='text-center itemsContainer'>
   <Link className=' text-decoration-none text-white ' data-aos="zoom-in-down" to={`/details/${mediaMovie}/${item.id}`}>
   <img src={imgsrc+item.poster_path} className="w-100 " alt="" />
   <div className="layer">
    <div>
    <p >{item.title}</p>
    <p className='overviewColor'>{item.overview.toString().slice(0,100)}</p>
    </div>
    </div>
   {item.vote_average==0?'':<div className="vote">{item.vote_average.toString().slice(0,3)}</div>}
   </Link>
  </div>
</div>
      )}
   </div>
 
 
 </div>



</>:''}
   
  </>}
   </>
  )
}
