import React , {useState,useEffect} from 'react'
import { createHashRouter,Navigate,RouterProvider } from 'react-router-dom'
import Routing from './../Routing/Routing';
import Home from './../Home/Home';
import Details from './../Details/Details';
import About from './../About/About';
import Register from './../Register/Register';
import Movies from './../Movies/Movies';
import Tvshow from './../Tvshow/Tvshow';
import Login from './../Login/Login';
import People from './../People/People';
import Notfound from './../Notfound/Notfound';
import jwtDecode from 'jwt-decode';
import TrendingProvider from '../Context/ApiContext';
import Search from '../Search/Search';


export default function App() {
useEffect(()=>{
  if(localStorage.getItem('token')!==null){
    saveCurrentUser()
  }
},[])
  
  let [user,setUser]=useState(null)
function saveCurrentUser(){
let encoded=localStorage.getItem('token')
let decoded=jwtDecode(encoded)
setUser(decoded)
console.log(decoded);

}

function logOut(){
  localStorage.removeItem('token')
  setUser(null)
}

function Protect(props){
  if(localStorage.getItem('token')===null)
  return <Navigate to='/login'/>
  else
    return props.children
  
}

let routs=createHashRouter([
  {path:'/',element:<Routing info={user} logOut={logOut}/>,children:[
    {path:'home',element:<Protect><Home/></Protect>},
    {path:'about',element:<Protect><About/></Protect>},
    {path:'register',element:<Register/>},
    {path:'tvshow',element:<Protect><Tvshow/></Protect>},
    {index:true,element:<Login saveCurrentUser={saveCurrentUser}/>},
    {path:'login',element:<Login saveCurrentUser={saveCurrentUser}/>},
    {path:'people',element:<Protect><People/></Protect>},
    {path:'search',element:<Protect><Search/></Protect>},
    {path:'movies',element:<Protect><Movies/></Protect> },
    {path:'details/:media/:id',element:<Protect><Details/></Protect> },
    
    {path:'*',element:<Notfound/>}
  ]}
])

  return (
    <TrendingProvider>
      <RouterProvider router={routs}/>
    </TrendingProvider>
   
  )
}
