import React, { useContext } from 'react'
import { Link } from 'react-router-dom';
import { Trending } from '../Context/ApiContext';
import { Helmet } from 'react-helmet';

export default function Tvshow() {
  let {tvArr,imgsrc ,load ,nextPage ,prevPage,setTvArr }=useContext(Trending)
  

  return (
    

   <div>
     <Helmet>
     <title>CimaMovie~TV Show</title>
      </Helmet>
    {load?<div className='d-flex min-vh-100 align-items-center justify-content-center  '><i className="fa-solid fa-circle-notch fa-spin fa-10x"></i></div>:<>
    
    <div className="row pt-4 g-3 section">
   {tvArr.map((item,index)=>
  <div className='col-md-3' key={index} >
  <div className='text-center itemsContainer'>
   <Link className='text-decoration-none text-white' data-aos="zoom-in-down" to={`/details/${item.media_type}/${item.id}`}>
   <img src={imgsrc+item.poster_path} className="w-100 " alt="" />
    <div className="layer">
    <div>
    <p >{item.name}</p>
    <p className='overviewColor'>{item.overview.toString().slice(0,100)}</p>
    </div>
    </div>
    {item.vote_average==0?'':<div className="vote">{item.vote_average.toString().slice(0,3)}</div>}
   </Link>
  </div>
</div>
      )}
   </div>

  <nav aria-label="Page navigation example">
  <ul className="pagination justify-content-end mt-3 ">
    <li className="page-item"><a className="page-link" onClick={()=>prevPage('tv', setTvArr)}>Previous</a></li>
    <li className="page-item"><a className="page-link" onClick={()=>nextPage('tv', setTvArr)}>Next</a></li>
  </ul>
</nav>
    
    </>}

 




   
   
   </div>
  )
}


