
import React, { useContext } from 'react'
import { Trending } from '../Context/ApiContext'
import { Helmet } from 'react-helmet'


export default function People() {
  let {peopleArr,imgsrc ,load ,nextPage ,prevPage ,setPeopleArr  }=useContext(Trending)
 
  
    return (
     <div>
      <Helmet>
     <title>CimaMovie~Actors/Actresses</title>
      </Helmet>
     {load?<div className='d-flex min-vh-100 align-items-center justify-content-center section '><i className="fa-solid fa-circle-notch fa-spin fa-10x"></i></div>:<>
     
     <div className="row pt-4 g-3 section">
     {peopleArr.filter((human) => human.profile_path != null).map((item,index)=>
    <div className='col-md-3' key={index} >
    <div className='text-center itemsContainer'>
    <img src={imgsrc+item.profile_path} className="w-100" alt="" />
     <div className="layer">
     <p>{item.name}</p>
     </div>
    </div>
  </div>
        )}
     </div>
  
    <nav aria-label="Page navigation example">
    <ul className="pagination justify-content-end mt-3 ">
      <li className="page-item"><a className="page-link" onClick={()=>prevPage('person', setPeopleArr)}>Previous</a></li>
      <li className="page-item"><a className="page-link" onClick={()=>nextPage('person', setPeopleArr)}>Next</a></li>
    </ul>
  </nav>
     </>}
    
  
  
  
  
     
     
     </div>
    )
}
