import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet'
import { useParams } from 'react-router-dom'

export default function Details() {
  window.scrollTo(0,0)
  const [load, setLoad] = useState(true)
  let[MovieData,setMovieData]=useState([])
  let imgsrc="https://image.tmdb.org/t/p/w500"
let {id , media}=useParams()
let [type,setType]=useState([])
console.log(MovieData);
  async function getMovieDetails(id ){
    setLoad(true)
    let {data} =await axios.get(`https://api.themoviedb.org/3/${media}/${id}?api_key=54a067480c37b2809731dbbe98a6e0ab&language=en-US`)
   console.log(data);
    setMovieData(data)
    setType(data.genres)
    setLoad(false)
  }
  useEffect(()=>{

    getMovieDetails(id)
  },[])
  function goBack(){
    window.history.go(-1)
  }

  return (

    <>
      <Helmet>
      {MovieData.name?<title>CimaMovie~{media}-{MovieData.name}</title>:""}
      {MovieData.title?<title>CimaMovie~{media}-{MovieData.title}</title>:""}
      </Helmet>

          {load?<div className='d-flex min-vh-100 align-items-center justify-content-center '><i className="fa-solid fa-circle-notch fa-spin fa-10x"></i></div>:
          <>
            <div className='min-vh-100 d-flex justify-content-center align-items-center'>
      
        
      <div className=' text-center '>
        <div className='details pt-4'>
        <img src={imgsrc+MovieData.poster_path} className='img-fluid rounded' alt="" />
        <div className=' mt-1 py-3'>
            {MovieData.homepage?<a href={MovieData.homepage} className="btn btn-danger px-5 py-2" target="_blank">Visit</a>:''}

            <button className='goback btn btn-danger mx-2 px-4 py-2 text-white ' onClick={goBack}>Go Back</button>
          </div>

        </div>
      
          <div className='pt-5'>
          <h1>{MovieData.title?MovieData.title:MovieData.name}</h1>
          <p className='pt-3 fs-4 text-white-50'>{MovieData.overview}</p>
          <h3>--Movie type</h3>
          <div className='row justify-content-center'>
          {type.map((elelement,index)=><div className='col-12 col-md-3' key={index}>
            <div > 
            <div className='alert alert-secondary text-center rounded-3 fs-5 '>
              {elelement.name}
            </div>
            </div>
          </div>)}

          </div>
        </div>
      
      </div>
  </div>
    
    
    
    
   
    
          
          
          
          
          
          </>}

    
    
    </>
    
    
 
   
    
  
  )
}
