import React from 'react'
import { Outlet } from 'react-router-dom';
import Navbar from './../Navbar/Navbar';


export default function Routing(props) {
  return (
    <>
    <Navbar data={props.info} logOut={props.logOut}/>
    <div className="container">
    <Outlet/>
    </div>
    </>
    
  )
}
