import axios from "axios";
import { createContext, useEffect, useState } from "react";

export let Trending = createContext(0);

export default function TrendingProvider(props){
    let [moviesArr, setMoviesArr] = useState([])
    let [tvArr, setTvArr] = useState([])
    let [peopleArr, setPeopleArr] = useState([])
    let imgsrc = "https://image.tmdb.org/t/p/w500"
    const [load, setLoad] = useState(true)
    let pageNum=new Array(5).fill(1).map((e,index)=>index+1)
    let [globalPageNumber,setglobalPageNumber]=useState(1)

  
    async function getTeendingData(mediatype, callback , pageNumber) {
      setLoad(true)
      setglobalPageNumber(pageNumber)
      let { data } = await axios.get(`https://api.themoviedb.org/3/trending/${mediatype}/day?api_key=54a067480c37b2809731dbbe98a6e0ab&page=${pageNumber}`)
      callback(data.results);
      setLoad(false)
    }
  
  
    //api call
    useEffect(() => {
      getTeendingData('movie', setMoviesArr , 1)
      getTeendingData('tv', setTvArr , 1)
      getTeendingData('person', setPeopleArr , 1)
    }, [])

    
    
    function nextPage(trendingType,trendingMethod){
      if(globalPageNumber===5){
        globalPageNumber=5
      }else{
        globalPageNumber++
        getTeendingData(trendingType, trendingMethod , globalPageNumber)
      }
      window.scrollTo(0,0)
    }
    function prevPage(trendingType , trendingMethod){
      if(globalPageNumber===1){
        globalPageNumber=1
      }else{
        globalPageNumber--;
        getTeendingData( trendingType ,trendingMethod ,globalPageNumber)
      }
      window.scrollTo(0,0)
    }





    return <Trending.Provider value={{moviesArr , tvArr , peopleArr ,imgsrc ,load ,nextPage ,prevPage,pageNum ,getTeendingData ,setMoviesArr ,setTvArr ,setPeopleArr, globalPageNumber ,setglobalPageNumber}}>
        {props.children}
    </Trending.Provider>
}
